import React from 'react';
import { Chip, makeStyles, Theme, createStyles, Typography, Button } from '@material-ui/core';
import DescriptionIcon from '@material-ui/icons/Description';

const statement: string = `Having learned from numerous outstanding teachers and mentors throughout my education, I gradually formed a teaching philosophy that includes four core elements: (1) Integrating anti-racist pedagogy in pursuit of inclusivity and diversity; (2) Teaching with multimodality to increase learner engagement; (3) Adapting to emerging technologies by nurturing algorithmic thinking; and (4) Promoting critical thinking through interdisciplinary learning. These principles are rooted in my commitment to creating inclusive classroom spaces and educational experiences that honor students’ educational goals, interests, and existing knowledge.`;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      paddingLeft: "15%", 
      paddingRight: "15%",
      paddingTop: "5%",
      paddingBottom: "5%"
    },
    interests: {
      paddingTop: "50px",
    },
    chipArray: {
      display: 'flex',
      flexWrap: 'wrap',
      listStyle: 'none',
      padding: theme.spacing(0.5),
      margin: 0,
    },
    chip: {
      fontSize: '0.9rem',
      cursor: "default",
      margin: theme.spacing(0.5),
    },
    button: {
      textTransform: 'none',
    },
  }),
);

const TeachingInterests = (props: {items: string[]}) => {
  const { items } = props;
  const classes = useStyles();

  return (
    <>
      <div className={classes.root}>
        <Typography gutterBottom variant="h5">
          Teaching Statement
        </Typography>
        <Typography gutterBottom variant="body1">
          {statement}
        </Typography>
        <Button 
          // size="small" 
          variant="outlined" 
          color="primary" 
          startIcon={<DescriptionIcon />}
          href={"../files/teachingStatement.pdf"}
          target="_blank"
          rel="noreferrer"
          className={classes.button}
        >
          View Teaching Statement
        </Button>
        <div className={classes.interests}>
          <Typography gutterBottom variant="h6">
            Teaching Interests
          </Typography>
          <ul className={classes.chipArray}>
            {items.map((item, index) => {
              return (
                <li key={index}>
                  <Chip label={item} clickable className={classes.chip}/>
                </li>
              )
            })}
          </ul>
        </div>
      </div>
    </>
  )
}

export default TeachingInterests;