import { makeStyles, Theme, createStyles } from '@material-ui/core';
import React from 'react';

import Courses from './Courses';
import StudentEvaluations from './StudentEvaluations';
import TeachingHeader from './TeachingHeader';
import TeachingInterests from './TeachingInterests';

import coursesData from '../data/courses.json';
import teachingInterestsData from '../data/teachingInterests.json';
import StudentEvaluationsData from '../data/studentEvaluations.json';

const useStyle = makeStyles((theme: Theme) => 
  createStyles({
    root: {
      marginTop: "80px",
      maxWidth: "1920px",
      margin: "auto",
      // alignItems: "end",
    }
  })
);

const TeachingPage: React.FunctionComponent = () => {
  const classes = useStyle();

  React.useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  return (
    <>
      {/* <TeachingHeader /> */}
      <div className={classes.root}>
        <TeachingInterests items={teachingInterestsData} />
        <Courses items={coursesData} />
        <StudentEvaluations items={StudentEvaluationsData} />
      </div>
    </>
  )
}

export default TeachingPage;