import React from 'react';
import { GalleryItem } from './GalleryColumn';
import { GridListTile, GridListTileBar, makeStyles, Theme, createStyles } from '@material-ui/core';

const useStyle = makeStyles((theme: Theme) => 
  createStyles({
    tile: {
      padding: "4px",
      width: "100%",
      height: "auto",
      listStyleType: "none",
      cursor: "zoom-in",
      "& :hover > img": {
        left: "0",
        transform: "scale(1.2)",
        transitionDuration: ".3s",
      },
    },
    image: {
      width: "100%",
      height: "auto",
    },
    filter: {
      position: "absolute",
      width: "100%",
      height: "100%",
      top: 0,
      left: 0,
      bottom: 0, 
      right: 0,
      boxShadow: "inset 0px 0px 100px 20px #000",
    },
    root: {
      heigth: "auto",
    },
    title: {
      fontSize: "12px",
      whiteSpace: "normal",
      overflow: "hidden",
      boxSizing: "content-box",
      maxLines: 2,
    },
  })
);

type GalleryTileProps = {
  item: GalleryItem,
  handleOpen: () => void,
}

const GalleryTile: React.FunctionComponent<GalleryTileProps> = (props: GalleryTileProps) => {
  const { item, handleOpen } = props;
  const classes = useStyle();
  const [focus, setFocus] = React.useState<boolean>(false);

  return (
    <div
      onMouseEnter={() => setFocus(true)}
      onMouseLeave={() => setFocus(false)}
      onClick={() => handleOpen()}
    >
      <GridListTile cols={1} className={classes.tile}>
        <img 
          src={item.path} 
          className={classes.image} 
          title={item.description}
          alt={item.description}
        />
        {focus && <>
          <div className={classes.filter}></div>
          <GridListTileBar
            title={
              <React.Fragment>
                <span className={classes.title}>{item.description}</span>
              </React.Fragment>
            }
            // subtitle={item.description}
            // actionIcon={
            //   <IconButton aria-label={`info about ${tile.title}`} className={classes.icon}>
            //     <InfoIcon />
            //   </IconButton>
            // }
            style={{ height: 'auto' }}
          />
        </>
        }
      </GridListTile>
    </div>
  )
}

export default GalleryTile;