import React from 'react';
import Typography from '@material-ui/core/Typography';
import makeStyles from '@material-ui/core/styles/makeStyles';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';

import interest1 from '../img/interest1.jpg';
import interest2 from '../img/interest2.jpg';
import interest3 from '../img/interest3.jpg';

const images: {
  [key: string]: string
} = {
  interest1: interest1,
  interest2: interest2,
  interest3: interest3,
}

const useStyles = makeStyles({
  root: {
    paddingLeft: "15%", 
    paddingRight: "15%",
    paddingTop: "50px",
    paddingBottom: "50px",
  },
  card: {
    minHeight: 450,
  },
  media: {
    height: 200,
  },
});

type InterestsProps = {
  items: InterestProps[],
}

type InterestProps = {
  uniqueKey: string,
  name: string,
  description: string,
}

const Interests: React.FunctionComponent<InterestsProps> = (props: InterestsProps) => {

  const classes = useStyles();
  const { items } = props;

  return (
    <>
      <div className={classes.root}>
        <Typography gutterBottom variant="h5">
          Research Interests
        </Typography>
        <Grid container spacing={5}>
          {items.map(item => {
            console.log(item);
            return (
              <Grid item={true} md>
                <InterestCard {...item} />
              </Grid>
            )
          })}
        </Grid>
      </div>
    </>
  )
};

export default Interests;

const InterestCard: React.FunctionComponent<InterestProps> = (props: InterestProps) => {
  const classes = useStyles();
  const { uniqueKey, name, description } = props;
  console.log(props);

  return (
    <Card className={classes.card}>
      {/* <CardActionArea> */}
        <CardMedia
          className={classes.media}
          image={images[uniqueKey]}
          title={name}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">{name}</Typography>
          <Typography variant="body2" color="textSecondary" component="p">{description}</Typography>
        </CardContent>
      {/* </CardActionArea> */}
      {/* <CardActions>
        <Button size="small" color="primary">
          Share
        </Button>
        <Button size="small" color="primary">
          Learn More
        </Button>
      </CardActions> */}
    </Card>
  );
}