import React from 'react';
import { makeStyles, Theme, createStyles, Box, Grid } from '@material-ui/core';
import Backdrop from '@material-ui/core/Backdrop/Backdrop';
import GalleryColumn, { GalleryItem } from './GalleryColumn';

import galleryData from '../data/gallery.json';

import img1 from '../img/gallery/1.jpg';
import img2 from '../img/gallery/2.jpg';
import img3 from '../img/gallery/3.jpg';
import img4 from '../img/gallery/4.jpg';
import img5 from '../img/gallery/5.jpg';
import img6 from '../img/gallery/6.jpg';
import img7 from '../img/gallery/7.jpg';
import img8 from '../img/gallery/8.jpg';
import img9 from '../img/gallery/9.jpg';
import img10 from '../img/gallery/10.jpg';
import img11 from '../img/gallery/11.jpg';
import img12 from '../img/gallery/12.jpg';
import img13 from '../img/gallery/13.jpg';
import img14 from '../img/gallery/14.jpg';
import img15 from '../img/gallery/15.jpg';
import img16 from '../img/gallery/16.jpg';
import img17 from '../img/gallery/17.jpg';
import img18 from '../img/gallery/18.jpg';
import img19 from '../img/gallery/19.jpg';
import GridListTile from '@material-ui/core/GridListTile/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar/GridListTileBar';

const galleryImages: string[] = [
  img1, img2, img3, img4, img5, img6, img7, img8, img9, img10, 
  img11, img12, img13, img14, img15, img16, img17, img18, img19
]

const useStyle = makeStyles((theme: Theme) => 
  createStyles({
    root: {
      marginTop: "80px",
      paddingLeft: "15%",
      paddingRight: "15%",
      maxWidth: "1920px",
      margin: "auto",
    },
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
      cursor: "zoom-out",
    },
    tile: {
      width: "auto",
      height: "auto",
      listStyleType: "none",
    },
    bar: {
      padding: "20px"
    },
    title: {
      heigth: "auto",
      fontSize: "20px",
      lineHeight: "32px",
      whiteSpace: "normal",
    }
  })
);

const GalleryPage: React.FunctionComponent = () => {
  const classes = useStyle();

  React.useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  const [openedIndex, setOpenedIndex] = React.useState<number>(-1);
  const handleClose = () => {
    setOpenedIndex(-1);
  }
  const handleOpen = (index: number) => {
    setOpenedIndex(index);
  }

  return (
    <div className={classes.root}>
      <Box display={{xs: "none", sm: "none", md: "none", lg: "block", xl: "none"}}>
        <Grid container justify="center">
          <GalleryColumn items={generateGalleryItems([0, 3, 6, 9, 12])} handleOpen={handleOpen} lg={4}/>
          <GalleryColumn items={generateGalleryItems([1, 4, 7, 10, 13, 15, 17])} handleOpen={handleOpen} lg={4}/>
          <GalleryColumn items={generateGalleryItems([2, 5, 8, 11, 14, 16, 18])} handleOpen={handleOpen} lg={4}/>
        </Grid>
      </Box>
      <Box display={{xs:"block", sm: "block", md: "block", lg: "none", xl: "block"}}>
        <Grid container justify="center">
          <GalleryColumn items={generateGalleryItems([0, 4, 8, 12, 16])} handleOpen={handleOpen} xl={3} md={6} sm={6} xs={12}/>
          <GalleryColumn items={generateGalleryItems([1, 5, 9, 13, 17])} handleOpen={handleOpen} xl={3} md={6} sm={6} xs={12}/>
          <GalleryColumn items={generateGalleryItems([2, 6, 10, 14, 18])} handleOpen={handleOpen} xl={3} md={6} sm={6} xs={12}/>
          <GalleryColumn items={generateGalleryItems([3, 7, 11, 15])} handleOpen={handleOpen} xl={3} md={6} sm={6} xs={12}/>
        </Grid>
      </Box>
      <Backdrop className={classes.backdrop} open={openedIndex >= 0} onClick={handleClose} transitionDuration={500}>
        <GridListTile cols={1} className={classes.tile}>
          <img 
            src={galleryImages[openedIndex]} 
            title={galleryData[openedIndex]?.description}
            alt={galleryData[openedIndex]?.description}
          />
          <GridListTileBar
              title={
                <React.Fragment>
                  <span className={classes.title}>{galleryData[openedIndex]?.description}</span>
                </React.Fragment>
              }
              // subtitle={item.description}
              // actionIcon={
              //   <IconButton aria-label={`info about ${tile.title}`} className={classes.icon}>
              //     <InfoIcon />
              //   </IconButton>
              // }
              className={classes.bar}
            />
        </GridListTile>
      </Backdrop>
    </div>
  )
}

export default GalleryPage;

function generateGalleryItems(indices: number[]): GalleryItem[] {
  const items: GalleryItem[] = indices.map((index) => {
    return {
      index: index,
      path: galleryImages[index],
      description: galleryData[index].description,
    } as GalleryItem;
  });
  return items;
}
