import React from 'react';
import makeStyles from '@material-ui/core/styles/makeStyles';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import { Theme } from '@material-ui/core/styles';

import hnu from '../img/hnu_logo.png';
import lu from '../img/lu_logo.png';
import ncsu from '../img/ncsu_logo.png';
import Box from '@material-ui/core/Box';

const logos: {
  [key: string]: string
} = {
  hnu: hnu,
  lu: lu,
  ncsu: ncsu,
}

const useStyle = makeStyles((theme: Theme) => ({
  root: {
    paddingLeft: "15%", 
    paddingRight: "15%",
    paddingTop: "50px",
    paddingBottom: "50px",
  },
  title: {
    paddingTop: "20px",
    paddingBottom: "20px",
  },
  body: {
    // padding: "5px 0",
  },
  container: {
    padding: 20,
  },
  avatarContainer: {
    marginTop: 0,
    marginRight: theme.spacing(3),
  },
  avatar: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
  divider: {
    height: "1px",
    boxShadow: "0 0 40px #fff",
  }
}));

type EducationProps = {
  items: EducationItemProps[],
}

type EducationItemProps = {
  uniqueKey: string,
  school: string,
  location: string,
  degree: string,
  time: string,
  hasAccomplished: boolean,
  isLast?: boolean,
}

const Education: React.FunctionComponent<EducationProps> = (props: EducationProps) => {
  const classes = useStyle();
  const { items } = props;

  return (
    <>
      <Divider className={classes.divider}></Divider>
      <div className={classes.root}>
        <Typography gutterBottom variant="h5" className={classes.title}>
          Education
        </Typography>
        <List>
          {items.map((item, index) => <EducationItem {...item} isLast={index === items.length-1} />)}
        </List>
      </div>
    </>
  );
}

export default Education;

const EducationItem: React.FunctionComponent<EducationItemProps> = (props: EducationItemProps) => {
  const classes = useStyle();
  const { uniqueKey, school, location, degree, time, isLast } = props;

  return (
    <>
      <ListItem alignItems="flex-start" divider={!isLast} className={classes.container}>
        <ListItemAvatar className={classes.avatarContainer}>
          <Avatar src={logos[uniqueKey]} className={classes.avatar}/>
        </ListItemAvatar>
        <ListItemText
          primary={degree}
          secondary={
            <React.Fragment>
              <Typography variant="body1" color="textSecondary" className={classes.body}>
                {school}
              </Typography>
            </React.Fragment>
          }
        />
        <Box textAlign="right">
          <ListItemText 
            primary={location} 
            secondary={
              <React.Fragment>
              <Typography variant="body1" color="textSecondary" className={classes.body}>
                {time}
              </Typography>
            </React.Fragment>
            }
          />
        </Box>
      </ListItem>
    </>
  )
};
