import React from 'react';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import Chip from '@material-ui/core/Chip';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import CardContent from '@material-ui/core/CardContent';
import AccountCircle from '@material-ui/icons/AccountCircle';
import CardHeader from '@material-ui/core/CardHeader';
import Box from '@material-ui/core/Box';


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      paddingLeft: "15%", 
      paddingRight: "15%",
      paddingTop: "50px",
      paddingBottom: "50px"
    },
    title: {
      paddingTop: "20px",
      paddingBottom: "20px",
    },
    card: {
      margin: "30px 0px",
    },
    chip: {
      padding: "0 2px",
    },
    noPaddingBottom: {
      paddingBottom: 0,
    },
    avatar: {
      width: theme.spacing(2.5),
      height: theme.spacing(2.5),
      marginRight: 10,
    },
  }),
);

type PublicationsProps = {
  items: PublicationItemProps[];
}

type PublicationItemProps = {
  title: string,
  journal: string,
  author: string,
  time: string,
  status: string,
  link: string,
}

const Publications: React.FunctionComponent<PublicationsProps> = (props: PublicationsProps) => {
  const classes = useStyles();
  const { items } = props;

  return (
    <>
      <Divider></Divider>
      <div className={classes.root}>
        <Typography gutterBottom variant="h5">
          Publications
        </Typography>
        {items.map((item, index) => <PublicationItem {...item} />)}
      </div>
    </>
  );
};

export default Publications;

const PublicationItem: React.FunctionComponent<PublicationItemProps> = (props: PublicationItemProps) => {
  const classes = useStyles();
  const { title, journal, author, time, status, link } = props;

  const [elevation, setElevation] = React.useState<number>(2);

  return (
    <div 
      onMouseEnter={() => setElevation(5)}
      onMouseLeave={() => setElevation(2)}
    >
      <Card elevation={elevation} className={classes.card}>
        <CardHeader
          title={
            <React.Fragment>
              <Typography variant="subtitle1">{title}</Typography>
            </React.Fragment>
          }
          subheader={
            <React.Fragment>
              {status && <Chip size="small" label={status} color="primary" className={classes.chip} />}
              {" "}
              {time && <Chip size="small" label={time} color="secondary" className={classes.chip} />}
            </React.Fragment>
          }
          className={classes.noPaddingBottom}
        />
        <CardContent className={classes.noPaddingBottom}>
          <Box display="flex">
            <Avatar className={classes.avatar}>
              <AccountCircle />
            </Avatar>
            <Typography variant="subtitle2" color="textSecondary">
              {author}
            </Typography>
          </Box>
          <Typography variant="body2" color="textPrimary">
            {journal}
          </Typography>
        </CardContent>
        {link && <CardActions>
          <Button color="primary" href={link} target="_blank" rel="noreferrer">
            Learn More
          </Button>
        </CardActions>}
      </Card>
    </div>
  )
};