import React from 'react';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import Chip from '@material-ui/core/Chip';
import Button from '@material-ui/core/Button';
import PhotoAlbumIcon from '@material-ui/icons/PhotoAlbum';
import PresentToAllIcon from '@material-ui/icons/PresentToAll';
import Box from '@material-ui/core/Box';

const GITHUB_PATH: string = '../files/';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      paddingLeft: "15%", 
      paddingRight: "15%",
      paddingTop: "5%",
      paddingBottom: "5%"
    },
    chip: {
      padding: "0 2px",
    },
    listItem: {
      padding: "22px 16px",
    },
    box: {
      display: "flex", 
      justifyContent: "space-between",
      marginBottom: "5px",
    }
  }),
);

type ConferencesProps = {
  items: ConferenceItemProps[];
}

type ConferenceItemProps = {
  title: string,
  conference: string,
  presenter: string,
  date: string,
  year: number,
  location: string,
  link: string,
  type: string,
  level: string,
  file: string,
  isLast?: boolean,
}

const Conferences: React.FunctionComponent<ConferencesProps> = (props: ConferencesProps) => {
  const classes = useStyles();
  const { items } = props;

  return (
    <>
      <Divider></Divider>
      <div className={classes.root}>
        <Typography gutterBottom variant="h5">
          Conferences
        </Typography>
        <List>
          {items.map((item, index) => <ConferenceItem {...item} isLast={index === items.length-1} />)}
        </List>
      </div>
    </>
  );
};

export default Conferences;

const ConferenceItem: React.FunctionComponent<ConferenceItemProps> = (props: ConferenceItemProps) => {
  const classes = useStyles();
  const { title, conference, presenter, year, type, file, isLast } = props;
  const isPoster: boolean = type === "poster";

  return (
    <>
      <ListItem className={classes.listItem}>
        <ListItemText
          primary={
            <React.Fragment>
              <Typography variant="subtitle1">{title}</Typography>
            </React.Fragment>
          }
          secondary={
            <React.Fragment>
              <Box className={classes.box}>
                {year && <Chip size="small" label={year} color="secondary" className={classes.chip} />}
                {file && <Button 
                  size="small" 
                  variant="outlined" 
                  color={isPoster ? "primary" : "secondary"} 
                  startIcon={isPoster ? <PhotoAlbumIcon /> : <PresentToAllIcon />}
                  href={GITHUB_PATH + file}
                  target="_blank"
                  rel="noreferrer"
                >
                  {type}
                </Button>}
              </Box>
              <Typography variant="subtitle2" color="textSecondary">
                {presenter}
              </Typography>
              <Typography variant="body2" color="textPrimary">
                {conference}
              </Typography>
            </React.Fragment>
          }
        />
      </ListItem>
      {!isLast && <Divider />}
    </>
  )
};