import React from 'react';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import CardHeader from '@material-ui/core/CardHeader';
import Card from '@material-ui/core/Card';
import Avatar from '@material-ui/core/Avatar';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Chip from '@material-ui/core/Chip';
import Button from '@material-ui/core/Button';

import ncsu from '../img/ncsu_logo.png';
import CE590 from '../img/CE590.png';
import ENG332 from '../img/ENG332.png';
import ENG333 from '../img/ENG333.png';
import IDS201 from '../img/IDS201.png';
import CardMedia from '@material-ui/core/CardMedia';

const images: {
  [key: string]: string
} = {
  CE590: CE590,
  ENG332: ENG332,
  ENG333: ENG333,
  IDS201: IDS201,
}

const GITHUB_PATH: string = '../files/';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      paddingLeft: "15%", 
      paddingRight: "15%",
      paddingTop: "5%",
      paddingBottom: "5%"
    },
    card: {

    },
    avatar: {

    },
    media: {
      height: 250,
    },
    // list: {
    //   backgroundColor: theme.palette.background.paper,
    // },
    // title: {
    //   paddingTop: "20px",
    //   paddingBottom: "20px",
    // },
    // listItem: {
    //   padding: "22px 16px",
    // },
    spaceBetween: {
      display: "flex", 
      justifyContent: "space-between"
    },
    zeroFlexBasis: {
      flexBasis: "0px"
    }
  })
);

type CoursesProps = {
  items: CourseItemPorps[];
}

type CourseItemPorps = {
  uniqueKey: string,
  course: string,
  time: string,
  program: string,
  university: string,
  description: string,
  role: string,
  fileSyllabus: string,
  fileAssignment: string,
}

const Courses: React.FunctionComponent<CoursesProps> = (props: CoursesProps) => {
  const classes = useStyles();
  const { items } = props;

  return (
    <>
      <Divider></Divider>
      <div className={classes.root}>
        <Typography gutterBottom variant="h5">
          Teaching Experiences
        </Typography>
        <Grid container spacing={5}>
          {items.map((item, index) => <CourseItem {...item} />)}
        </Grid>
      </div>
    </>
  )
};

export default Courses;

const CourseItem: React.FunctionComponent<CourseItemPorps> = (props: CourseItemPorps) => {
  const classes = useStyles();
  const { uniqueKey, course, time, program, description, role, fileSyllabus, fileAssignment } = props;

  return (
    <Grid item md={6}>
      <Card className={classes.card}>
        <CardHeader
          avatar={
            <Avatar src={ncsu} className={classes.avatar}/>
          }
          title={
            <React.Fragment>
              <Typography variant="subtitle1" color="textPrimary">
                {course}
              </Typography>
              <Typography variant="subtitle2" color="textPrimary">
                {program}
              </Typography>
            </React.Fragment>
          }
          subheader={
            <React.Fragment>
            <Grid container className={classes.spaceBetween}>
              <Grid item md={12} lg={7}>
                <Typography variant="subtitle1" color="textSecondary">
                  {time}
                </Typography>
              </Grid>
              <Grid item md={12} lg={5} className={classes.zeroFlexBasis}>
                <Chip size="small" variant="outlined" color="primary" label={role}/>
              </Grid>
            </Grid>
            </React.Fragment>
          }
        />
        <CardMedia
          className={classes.media}
          image={images[uniqueKey]}
          title={course}
        />
        <CardContent>
          <Typography variant="body2" color="textSecondary" component="p">
            {description}
          </Typography>
        </CardContent>
        <CardActions>
          <Button href={GITHUB_PATH + fileSyllabus} target="_blank" size="small" color="primary">
            Syllabus
          </Button>
          <Button href={GITHUB_PATH + fileAssignment} target="_blank" size="small" color="primary">
            Assignment
          </Button>
          {/* <IconButton aria-label="add to favorites">
            <FavoriteIcon />
          </IconButton>
          <IconButton aria-label="share">
            <ShareIcon />
          </IconButton>
          <IconButton
            className={clsx(classes.expand, {
              [classes.expandOpen]: expanded,
            })}
            onClick={handleExpandClick}
            aria-expanded={expanded}
            aria-label="show more"
          >
            <ExpandMoreIcon />
          </IconButton> */}
        </CardActions>
      </Card>
    </Grid>
  )
};