import React from 'react';
import MenuIcon from '@material-ui/icons/Menu';
import { makeStyles, Theme, useScrollTrigger, AppBar, Toolbar, Typography, Button, IconButton } from '@material-ui/core';
import ButtonGroup from '@material-ui/core/ButtonGroup/ButtonGroup';
import { Link } from 'react-router-dom';

const useStyle = makeStyles((theme: Theme) => ({
  root: {
    padding: "0 10%",
    minHeight: "80px",
    justifyContent: "center",
    backgroundColor: theme.palette.success.contrastText,
  },
  title: {
    flexGrow: 1,
    textTransform: "capitalize",
    color: theme.palette.common.white,
    textDecoration: "none",
  },
  menu: {
    padding: "0px",
  },
  buttonGroup: {
    width: "100%",
    borderRadius: "0px",
    backgroundColor: theme.palette.grey["100"],
  },
  button: {
    textTransform: "capitalize",
    height: theme.spacing(5),
    width: "100%",
  },
}));


const NavigationBarMini: React.FunctionComponent = () => {
  const classes = useStyle();

  const [open, setOpen] = React.useState<boolean>(false);

  return (
    <ElevationScroll>
      <AppBar>
        <Toolbar className={classes.root}>
          <Typography variant="h6" className={classes.title} component={Link} to={'/'}>
            Yeqing Kong
          </Typography>
          <IconButton 
            edge="end" 
            onClick={() => setOpen(!open)}
            color="inherit"
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
        {open && <Toolbar className={classes.menu}>
          <ButtonGroup
            orientation="vertical"
            variant="text"
            className={classes.buttonGroup}
          >
            <Button component={Link} to={'/'} className={classes.button} href="/" onClick={() => setOpen(false)}>Home</Button>
            <Button className={classes.button} href="../files/cv.pdf" target="_blank" onClick={() => setOpen(false)}>CV</Button>
            <Button component={Link} to={'/research'} className={classes.button} href="/research" onClick={() => setOpen(false)}>Research</Button>
            <Button component={Link} to={'/teaching'} className={classes.button} href="/teaching" onClick={() => setOpen(false)}>Teaching</Button>
            {/* <Button component={Link} to={'/gallery'} className={classes.button} href="/gallery" onClick={() => setOpen(false)}>Gallery</Button> */}
          </ButtonGroup>
        </Toolbar>}
      </AppBar>
    </ElevationScroll>
  )
};

const ElevationScroll = (props: {children: React.ReactElement}) => {
  const { children } = props;

  const trigger = useScrollTrigger();

  return React.cloneElement(children, {
    elevation: trigger ? 4 : 0,
  })
};

export default NavigationBarMini;