import React from 'react';
import makeStyles from '@material-ui/core/styles/makeStyles';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import DescriptionIcon from '@material-ui/icons/Description';

const shortBioText1: string = `I'm a Ph.D. candidate in the program of `;
const shortBioLink1 = {
  link: "https://crdm.chass.ncsu.edu/",
  text: "Communication, Rhetoric, and Digital Media",
};
const shortBioText2: string = ` at North Carolina State University. My research and teaching interests 
                               include international professional and technical communication, risk 
                               communication, rhetoric of science and technology, health communication, 
                               digital and visual rhetorics, and social justice. My scholarly work has 
                               appeared in `;
const journal1: string = `Proceedings of the ACM International Conference on the Design of Communication`;
const journal2: string = `China Media Research`;
const journal3: string = `Proceedings of IEEE International Professional Communication Conference`;

const useStyle = makeStyles({
  root: {
    // marginLeft: "5%",
    // background: "white",
    // boxShadow: "10 10 5 #666",
    // paddingLeft: "5%", 
    // paddingRight: "5%",
    // paddingTop: "50px",
    // paddingBottom: "50px"
  },
  title: {
    padding: "10px 0px",
  },
  body: {
    textAlign: "left",
  },
  italics: {
    fontStyle: 'italic',
  }
});

const Biography: React.FunctionComponent = () => {
  const classes = useStyle();
  return (
    <>
      <Typography variant="h6" className={classes.title}>
        Biography
      </Typography>
      <Typography gutterBottom variant="body1" className={classes.body}>
        {shortBioText1}
        <Link href={shortBioLink1.link}>{shortBioLink1.text}</Link>
        {shortBioText2}
        <span className={classes.italics}>{journal1}</span>
        {`, `}
        <span className={classes.italics}>{journal2}</span>
        {`, and `}
        <span className={classes.italics}>{journal3}</span>
        {`.`}
      </Typography>
      <Button 
        // size="small" 
        variant="outlined" 
        color="primary" 
        startIcon={<DescriptionIcon />}
        href={"../files/cv.pdf"}
        target="_blank"
        rel="noreferrer"
      >
        View CV
      </Button>
    </>
  )
}

export default Biography;