import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { makeStyles, Theme, useScrollTrigger, AppBar, withStyles, createStyles, Toolbar, Typography, Button } from '@material-ui/core';

const useStyle = makeStyles((theme: Theme) => ({
  root: {
    padding: "0 15%",
    height: "80px",
    justifyContent: "center",
    backgroundColor: theme.palette.success.contrastText,
    display: "flex",
  },
  name: {
    textTransform: "capitalize",
    color: theme.palette.common.white,
  },
}));

interface StyledTabsProps {
  value: number;
  onChange: (event: React.ChangeEvent<{}>, newValue: number) => void;
}

const StyledTabs = withStyles({
  indicator: {
    display: 'flex',
    justifyContent: 'center',
    backgroundColor: 'transparent',
    '& > span': {
      maxWidth: 40,
      width: '100%',
      backgroundColor: '#635ee7',
    },
  },
})((props: StyledTabsProps) => <Tabs {...props} TabIndicatorProps={{ children: <span /> }} />);

interface StyledTabProps {
  label: string;
  component: any;
  to?: string;
  href?: string;
  target?: string;
}

const StyledTab = withStyles((theme: Theme) =>
  createStyles({
    root: {
      textTransform: 'capitalize',
      color: theme.palette.common.white,
      fontWeight: theme.typography.fontWeightRegular,
      fontSize: theme.typography.pxToRem(15),
      marginRight: theme.spacing(0.5),
      marginLeft: theme.spacing(0.5),
      '&:focus': {
        opacity: 1,
      },
    },
  }),
)((props: StyledTabProps) => <Tab disableRipple {...props} />);

const NavigationBar: React.FunctionComponent = () => {
  const classes = useStyle();
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    if (newValue !== 1) {
      setValue(newValue);
    }
  };

  return (
    <ElevationScroll>
      <AppBar className={classes.root}>
        <Toolbar>
          <Button href="/">
            <Typography variant="h6" className={classes.name}>
              Yeqing Kong
            </Typography>
          </Button>
          <StyledTabs value={value} onChange={handleChange}>
            <StyledTab label="Home" component={RouterLink} to={"/"} />
            <StyledTab label="CV" component="a" href="../files/cv.pdf" target="_blank" />
            <StyledTab label="Research" component={RouterLink} to={"/research"} />
            <StyledTab label="Teaching" component={RouterLink} to={"/teaching"} />
            {/* <StyledTab label="Gallery" component={RouterLink} to={"/gallery"} /> */}
          </StyledTabs>
        </Toolbar>
      </AppBar>
    </ElevationScroll>
  )
};

const ElevationScroll = (props: {children: React.ReactElement}) => {
  const { children } = props;

  const trigger = useScrollTrigger();

  return React.cloneElement(children, {
    elevation: trigger ? 4 : 0,
  })
};

export default NavigationBar;