import React from 'react';
import Carousel, { arrowsPlugin, autoplayPlugin, Dots, slidesToShowPlugin } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';
import EvaluationCard, { StudentEvaluationsItem } from './EvaluationCard';
import { makeStyles, Theme, createStyles, Typography, Divider } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) => 
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      paddingLeft: "15%", 
      paddingRight: "15%",
      paddingTop: "5%",
      paddingBottom: "5%"
    }
  })
);

const StudentEvaluations = (props: {items: StudentEvaluationsItem[]}) => {
  const { items } = props;
  const classes = useStyles();

  const [value, setValue] = React.useState<number>(0);

  return (
    <>
      <Divider></Divider>
      <div className={classes.root}>
        <Typography gutterBottom variant="h5">
          Student Evaluations
        </Typography>
        {renderTrash(items, value, setValue)}
      </div>
    </>
  )
}

const renderTrash = (
  items: StudentEvaluationsItem[],
  value: number,
  setValue: React.Dispatch<React.SetStateAction<number>>
) => {
  try {
    const trash: JSX.Element = (
      <>
        <Carousel
          plugins={[
            'arrows',
            'clickToChange',
            'centered',
            'infinite',
            {
              resolve: slidesToShowPlugin,
              options: {
                numberOfSlides: 2
              }
            },
            {
              resolve: autoplayPlugin,
              options: {
                interval: 5000,
              }
            },
          ]}
          animationSpeed={1000}
          slides={items.map((item, index) => <EvaluationCard item={item} key={index}/>)}
          // arrows={true}
          // dots={true}
          // slidesPerPage={2}
          value={value}
          onChange={setValue}
        >
        </Carousel>
        <Dots 
          value={value} 
          onChange={setValue} 
          number={items.length} />
      </>
    );
    return trash;
  } catch (e) {
    console.log(e);
  }
}

export default StudentEvaluations;
