import React from 'react';
import { IconButton, makeStyles, Theme, createStyles } from "@material-ui/core";

import contactData from '../data/contact.json';

import GoogleScholarLogo from '../img/gs_logo.png';
import ResearchGateLogo from '../img/rg_logo.png';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import OrcidLogo from '../img/orcid_logo.svg';
import TwitterIcon from '@material-ui/icons/Twitter';
import GitHubIcon from '@material-ui/icons/GitHub';
import { MediaType } from '../homepage/constants';

const useStyle = makeStyles((theme: Theme) => 
  createStyles({
    root: {
      // [theme.breakpoints.down('md')]: {
        // textAlign: "center",
      // }
    },
    coloredImage: {
      width: "30px",
    },
    grayscaleImage: {
      width: "20px",
      filter: "grayscale(100%) brightness(0.7) contrast(2.5)",
    },
    button: {
      color: "white"
    },
    listItem: {
      display: "flex",
      justifyContent: "right",
      alignItems: "center",
    }
  })
);

const newContactData = contactData.map(item => (
  {
    type: MediaType[item.type as keyof typeof MediaType],
    name: item.name,
    link: item.link,
  }
));

const ContactBlock: React.FunctionComponent = () => {
  const classes = useStyle();

  return (
    <>
      {newContactData.map(item => {
        return (
          <IconButton href={item.link} target="_blank" title={item.name}>
            {renderGrayscaleIconButton(item.type, classes.grayscaleImage, "small" /* fontSize */)}
          </IconButton>
        )
      })}
    </>
  )
}

export default ContactBlock;

const renderGrayscaleIconButton = (
  type: MediaType,
  iconClass: string,
  fontSize: "inherit" | "default" | "large" | "small" | undefined,
) => {
  switch(type) {
    case MediaType.ResearchGate:
      return <img src={ResearchGateLogo} className={iconClass} alt="research gate"></img>
    case MediaType.Orcid:
      return <img src={OrcidLogo} className={iconClass} alt="orcid"></img>
    case MediaType.GoogleScholar:
      return <img src={GoogleScholarLogo} className={iconClass} alt="google scholar"></img>
    case MediaType.LinkedIn:
      return <LinkedInIcon fontSize={fontSize} />
    case MediaType.Twitter:
      return <TwitterIcon fontSize={fontSize} />
    case MediaType.Github:
      return <GitHubIcon fontSize={fontSize} />
  }
};
