import React from 'react';
import { makeStyles, Theme, Typography, Chip, createStyles } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      paddingLeft: "15%", 
      paddingRight: "15%",
      paddingTop: "5%",
      paddingBottom: "5%"
    },
    title: {
      marginTop: "20px",
      padding: "10px 0px",
    },
    chipArray: {
      display: 'flex',
      flexWrap: 'wrap',
      listStyle: 'none',
      padding: theme.spacing(0.5),
      margin: 0,
    },
    chip: {
      cursor: "default",
      margin: theme.spacing(0.5),
    },
  }),
);

const Specialties = (props: { specialties: string[] }) => {
  const { specialties } = props;
  const classes = useStyles();
  
  return (
    <>
      <Typography variant="h6" className={classes.title}>
        Specialties
      </Typography>
      <ul className={classes.chipArray}>
        {specialties.map((specialty, index) => {
          return (
            <li key={index}>
              <Chip variant="outlined" color="primary" label={specialty} clickable className={classes.chip}/>
            </li>
          )
        })}
      </ul>
    </>
  )
}

export default Specialties;